//状态数据管理仓库，存储共享数据
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'测试'},
        ],
        isShowMoreMenus:false,  //actionsheet默认显示与否
        // moreMenus:      //如果是未登录状态，显示‘登录’，如果是已登录状态，显示个人中心，消息通知，登出
        isLogined:false,    //登陆状态，默认未登录
        unloginedMenus:{
            login:'登录',
        },
        loginedMenus:{
            logout:'登出',
            center:'个人中心',
            msg:'消息通知',
            create:'发表新话题'
        },
        loginname: '', //登录用户名
        accesstoken: '', //登录的账号
        authorId: '' //登录id
    },
    getters:{
        moreMenus(state){   //参数state表示当前store对象的state数据
            if(state.isLogined){
                return state.loginedMenus
            }
            return state.unloginedMenus
        }
    },
    mutations:{
        // 修改actionsheet的显示与否
        mutationIsShowMoreMenus(state,payload){
            state.isShowMoreMenus=payload;
        },
        // 修改登陆状态
        mutationIsLogined(state) {
            state.isLogined = true;
        },
        // 修改登录名
        mutationLoginname:function(state,payload){
            state.loginname=payload;
        },
        // 修改accesstoken账号
        mutationAccesstoken:function(state,payload){
            state.accesstoken=payload;
        },
        // 修改登陆账号id
        mutationAuthorId:function(state,payload){
            state.authorId=payload;
        },
        // 登出操作
        mutationLogout:function(state){
            state.isLogined=false;
            state.loginname='';
            state.accesstoken='';
            state.authorId='';

        }
    },
    actions:{}
})

export default store;


