
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
props: ['loginname'],
components:{
Card,
Group,
Cell
},
data(){
return{
userInfo:null,
collect_topics:[],
lists:[]
}
},

beforeRouteEnter(to,from,next){
next(function(vm){

vm.$http.get('/user/'+vm.loginname).then(function(res){
console.log(res.data.data);
vm.userInfo=res.data.data;
})

vm.$http.get('/topic_collect/'+vm.loginname).then(function(res){
vm.collect_topics = res.data.data
})
})
},
beforeRouteUpdate(to,from,next){
var app = this;
this.$http.get('/user/'+to.params.loginname).then(function(res){
app.userInfo=res.data.data
});

this.$http.get('/topic_collect/'+to.params.loginname).then(function(res){
app.collect_topics = res.data.data
});
next();
}
}



