
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from "mavon-editor";
import axios from 'axios';
import "mavon-editor/dist/css/index.css";
export default {
components: {
Group,
XTextarea,
mavonEditor,
XButton,
Toast
},
data(){
return{
title:'',
content:'',
toastText:'',
isShowToast:false,
toolbars: {
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
strikethrough: true, // 中划线
mark: true, // 标记
superscript: true, // 上角标
subscript: true, // 下角标
quote: true, // 引用
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
table: true, // 表格
fullscreen: true, // 全屏编辑
readmodel: true, // 沉浸式阅读
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 1.3.5 */
undo: true, // 上一步
redo: true, // 下一步
trash: true, // 清空
save: false, // 保存（触发events中的save事件）
/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true 
}

}
},
methods: {

uploadImage(pos,file){
var app = this;
console.log(pos);   
console.log(pos);   

var params = new FormData();
params.append('smfile',file);
axios.post('https://vip1.loli.net',params,{

headers:{'Content-type':'multipart/form-data'}
}).then(function(res){

app.$refs.md.$img2Url(pos,res.data.message.url)
})
},
addtopic(){
var app = this;
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'   
}).then(function(res){
console.log(res);
app.$router.replace('/topiclist/dev')
}).catch(function(error){
app.isShowToast=true;
app.toastText='内容不能为空或者已经超出限制'
})
}
}
}



